.introMain {
    position: relative;
    padding: 20px 0;
    min-height: 100vh;
    background-image: url('../images/intro-slider/intro-slider-1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    &-title {
        color: #fff;
        font-size: 89px;
        font-weight: 700;
        margin-top: 50px;
    }
}

.introPages {
    background-image: url("../images/intro-slider/intro-2.jpg");
    position: relative;
    padding: 20px 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    min-height: 50vh;
}




@media screen and (max-width: 1400px) {
    .introMain {
        padding: 20px 0;
        min-height: 100vh;
        background-image: url('../images/intro-slider/intro-slider-1.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        &-title {
            color: #fff;
            font-size: 58px;
            font-weight: 700;
            margin-top: 80px;
            text-align: center;
        }
    }
}

@media screen and (max-width: 1200px) {
    .introMain {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 20px 0;
        min-height: 100vh;
        background-image: url('../images/intro-slider/intro-slider-1.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        &-title {

            color: #fff;
            font-size: 58px;
            font-weight: 700;
            text-align: center;
        }
    }
}

@media screen and (max-width: 992px) {
    .introMain {
        &-title {
            color: #fff;
            font-size: 42px;
            font-weight: 700;
            text-align: center;
        }
    }
}

@media screen and (max-width: 576px) {
    .introMain {
        &-title {
            color: #fff;
            font-size: 30px;
            font-weight: 700;
            text-align: center;
        }
    }
}

@media screen and (max-width: 400px) {
    .introMain {
        &-title {
            color: #fff;
            font-size: 26px;
            font-weight: 700;
            text-align: center;
        }
    }
}